import { Engine } from "./Core/Engine";
import { Logger } from "./Utils/Logger";

// DOM
document.addEventListener('DOMContentLoaded', () => {
    // 
    Engine.Init();

    // 
    function mainLoop() {
        // 
        Engine.Update();
        // 
        Engine.Render();
        // 
        Logger.printLogs();
        // 
        requestAnimationFrame(mainLoop);
    }
    // 
    requestAnimationFrame(mainLoop);
});